  // if (route.path !== '/') {
  //   indexScrollTop = document.body.scrollTop;
  // }
  //document.title = route.meta.title || document.title;
  //next();
//});

router.afterEach(route => {
  // if (route.path !== '/') {
  //   document.body.scrollTop = 0;
  // } else {
  //   Vue.nextTick(() => {
  //     document.body.scrollTop = indexScrollTop;
  //   });
  // }
  document.body.scrollTop = 0;
});



