const jobUrl = 'https://www.weihaizp.com:8070'
const buildUrl = 'https://www.weihaizp.com:8071'

const ajaxUrl = {
    'hotjob': jobUrl+'/api/recruit/hotjob',
    'job_detail': jobUrl+'/api/recruit/jobdetail',
    'partjob': jobUrl+'/api/recruit/partjob',
    'partjob_detail': jobUrl+'/api/recruit/jobpartdetail',
    'build_list': buildUrl+'/buildings/get-building-list',
    'build_info': buildUrl+'/buildings/get-building-info',
    'reserve': buildUrl+'/buildings/predetermine',
    'build_room_list': buildUrl+'/buildings/get-building-room-list',
    'build_room_info': buildUrl+'/buildings/get-building-room-info',
    'create_order': buildUrl+ '/order/create-order',
    'order_info': buildUrl+ '/order/get-order-info',
    'order_list': buildUrl+ '/order/get-order-list',
    'order_renew': buildUrl+ '/order/renew-order',
    'captcha': buildUrl+'/sms/send-code',
    'login': buildUrl+'/user/login',
    'update_user_info': buildUrl+'/user/update-user-info'
}

export default ajaxUrl


