const Util = {
    isLogin () {
        let now_time = Date.now();
        let exp_time = localStorage.getItem("token_expiration_time") || 0;

        let token = this.getToken();
        if(token && (exp_time > now_time)) {
            return true;
        }

        return false;
    },

    getToken () {
        let token = localStorage.getItem("token");

        return token;
    },

    getToday () {
        let now = new Date();
        let year = String(now.getFullYear())
        let month = this.zeroPad(now.getMonth()+1)
        let today = this.zeroPad(now.getDate())

        return [year, month, today];
    },

    getTomorrow () {
        let now = new Date();
        let tomorrow = this.zeroPad(now.getDate()+1)
        let today = this.getToday();

        today[2] = tomorrow;

        return today;
    },


    formatDate (now) { 
        return new Date(parseInt(now) * 1000).toLocaleString().replace(/:\d{1,2}$/,' ');
    },

    formatDay (now) { 
        let day = this.formatDate(now);

        return day.split(' ')[0].split('/').join('-');
    },

    //默认地址
    defaultCity () {
        return ["北京市", "北京市"];
    },

    /*
     * 存储地址
     * city Array
     */
    storageCity (add) {
        let expiration = 2*86400; //2天

        if(this.isArray(add)){
            localStorage.setItem("user-location", add.toString());
            localStorage.setItem("user-location-exptime", Date.now() + expiration);
        }
    },

    getStorageCity() {
        let now_time = Date.now();
        let exp_time = localStorage.getItem("user-location-exptime") || 0;

        if(exp_time > now_time) {
            let loc = localStorage.getItem("user-location");
            if(loc && loc.indexOf(',') != -1){
                return loc.split(',');
            }
            return null;
        }
        return null;
    },

    //小于10补0
    zeroPad (n) {
        return String(n < 10 ? '0' + n : n)
    },

    isObject (o) {
        return Object.prototype.toString.call(o) === '[object Object]'
    },

    isArray (o) {
        return Object.prototype.toString.call(o)=='[object Array]';
    }
}



export default Util




